<template>
		<div class="file-drop-zone text-center" v-on:dragenter="dropIn" v-on:dragover="dropIn" v-on:dragleave="dropOut" v-on:drop="dropFile">
			<div class="drop-zone-file-select" v-show="!existingPackageIcon && !selectedPackageIcon" @click="openFileSelector">
				<span class="select-file-text">
					<label for="select-package">Select File</label>
					<label>(.png)</label>
					
					<input type="file" ref="fileSelector" @change="fileSelected" accept="image/png">
				</span>
			</div>
			<div class="icon-preview" ref="iconPreview" @click="clearPackageIcon" v-show="existingPackageIcon || selectedPackageIcon" :style="{'background-image' : selectedIconBlob ? `url(${selectedIconBlob})` : `url(/resources/icons/${existingPackageIcon})` }"></div>
		</div>
</template>

<style lang="less" scoped>

</style>

<script>
import * as BlobUtil from 'blob-util'

export default {
	name: "PackageIconSelector",
	props: ["value", "packageIcon"],
	data() {
		return {
			selectedPackageIcon: this.$props.value,
			selectedIconBlob: null,
			existingPackageIcon: this.$props.packageIcon !== null ? this.$props.packageIcon : null
		}
	},
	methods: {
		dropIn(e) {
			e.preventDefault();
			e.stopPropagation();
			
			e.target.classList.add("drop-in");
		},
		dropOut(e) {
			e.preventDefault();
			e.stopPropagation();
			
			e.target.classList.remove("drop-in");
		},
		dropFile(e) {
			e.preventDefault();
			e.stopPropagation();
			
			e.target.classList.remove("drop-in");
			
			let file = e.dataTransfer.files[0];
			if (["image/png"].includes(file.type)) {
				this.handleFile(file);
			}
		},
		
		openFileSelector() {
			this.$refs["fileSelector"].click();
		},
		clearPackageIcon() {
			this.existingPackageIcon = null;
			this.selectedPackageIcon = null;
			this.$emit('input', this.selectedPackageIcon);
		},
		fileSelected(e) {
			let file = e.target.files[0];
			this.handleFile(file);
		},
		handleFile(file) {
			const self = this;
			
			this.selectedPackageIcon = file;
			this.$emit('input', this.selectedPackageIcon);
			
			const ref = this.$refs.iconPreview;

			const reader = new FileReader();
			reader.onload = function(progress) {
				const blob = window.URL.createObjectURL(BlobUtil.arrayBufferToBlob(progress.target.result));
				self.selectedIconBlob = blob;
				ref.style.backgroundImage = `url(${blob})`;
			}
			reader.readAsArrayBuffer(file);
		}
	}
}
</script>