<template>
	<div>
		<vue-headful title="New Package – FESTIVAL Pack'd" />
		<div class="container">
			<div class="row">
				<div class="col-md-3">
					<Nav />
				</div>
				<div class="col-md-9">
					<div class="dashhead mb-3">
						<div class="dashhead-titles">
							<h6 class="dashhead-subtitle">Packages</h6>
							<h2 class="dashhead-title">New Package</h2>
						</div>
					</div>
					
					<div class="row">
						<div class="col-6 text-left">
							<button class="btn btn-danger" :disabled="!$v.$anyDirty || isDoingWork" @click="cancel()">
								<span class="icon icon-squared-cross" />
								Cancel
							</button>
						</div>
						<div class="col-6 row p-0 justify-content-end text-right">
							<div class="col p-0 pr-3" v-if="isDoingWork">
								<div class="loading-indicator" />
							</div>
							<div class="col-auto p-0">
								<button class="btn btn-primary" @click="savePackage()" :disabled="$v.$invalid || !$v.$anyDirty || isDoingWork">
									<span class="icon icon-save" />
									Save
								</button>
							</div>
						</div>
					</div>
					
					<form name="createPackageForm" novalidate>
						<h4 class="my-4">General Info</h4>
						<div class="row">
							<div class="col-md-6 form-group">
								<label>Package Name</label>
								<input type="text" class="form-control" placeholder="Example Package" v-model="packageData.name" maxlength="50" autocomplete="off" @input="$v.$touch()" :disabled="isDoingWork">
								<p class="text-muted text-right m-b-0">{{50 - packageData.name.length}}</p>
							</div>
							<div class="col-md-6 form-group">
								<label>Bundle Identifier</label>
								<input type="text" class="form-control" placeholder="com.example.package" v-model="packageData.identifier" maxlength="50" autocomplete="off" @input="$v.$touch()"  :disabled="isDoingWork">
								<p class="text-muted text-right m-b-0">{{50 - packageData.identifier.length}}</p>
							</div>
						</div>
						<div class="row">
							<div class="col-md-6 form-group">
								<label for="login-password">Short Description</label>
								<textarea class="form-control" v-model="packageData.shortDescription" maxlength="255" autocomplete="off" @input="$v.$touch()" style="height: 100px" :disabled="isDoingWork" />
								<p class="text-muted text-right m-b-0">{{255 - packageData.shortDescription.length}}</p>

								<label>Description</label>
								<textarea class="form-control" v-model="packageData.detailedDescription" maxlength="4000" autocomplete="off" @input="$v.$touch()" style="height: 250px" :disabled="isDoingWork" />
								
								<div class="row">
									<div class="col-9">
										<p class="text-muted text-left m-b-0">Descriptions support HTML</p>
									</div>
									
									<div class="col-3">
										<p class="text-muted text-right m-b-0">{{4000 - packageData.detailedDescription.length}}</p>
									</div>
								</div>
							</div>
							<div class="col-md-6 form-group">
								<label>Version</label>
								<input type="text" class="form-control mb-4" v-model="packageData.releaseVersion" value="1.0" maxlength="30" autocomplete="off" @input="$v.$touch()" :disabled="isDoingWork">

								<label>Release Description (optional)</label>
								<textarea class="form-control mb-4" v-model="packageData.releaseDescription"  autocomplete="off" @input="$v.$touch()" style="height: 100px" :disabled="isDoingWork" />

								<label>Support URL</label>
								<input type="text" class="form-control mb-4" v-model="packageData.bugsReportURL" placeholder="http://example.com (optional)" autocomplete="off" @input="$v.$touch()" :disabled="isDoingWork">

								<div class="row">
									<div class="col-6">
										<label>Minimum OS</label>
										<input type="text" class="form-control" placeholder="11.0" v-model="packageData.minOSVersion" maxlength="30" autocomplete="off" @input="$v.$touch()" :disabled="isDoingWork">
									</div>
									<div class="col-6">
										<label>Maximum OS</label>
										<input type="text" class="form-control" placeholder="11.3.1" v-model="packageData.maxOSVersion" maxlength="30" @input="$v.$touch()" autocomplete="off" :disabled="isDoingWork">
									</div>
								</div>
							</div>
						</div>
						<div class="row">
							<div class="col-6 col-md-3 mt-4 mt-md-0">
								<h4 class="mb-4">Store Icon</h4>
								<PackageIconSelector :class="{'disabled': isDoingWork}" v-model="selectedPackageIcon" @input="$v.$touch()" />
							</div>
							<div class="col-6 col-md-3 mt-4 mt-md-0">
								<h4 class="mb-4">Package File</h4>
								<PackageFileSelector :class="{'disabled': isDoingWork}" v-model="selectedPackageFile" @input="$v.$touch()" />
							</div>
							<div class="list-group col-md-6 mt-4 mt-md-0 m-b px-3">
								<h4 class="mb-4">Options</h4>
								<div class="list-group-item d-flex justify-content-between align-items-center">
									<div class="col-auto p-0">
										<p class="m-0">Release this package automatically</p>
									</div>
									<div class="col-auto p-0">
										<SwitchCheckbox :class="{'disabled': isDoingWork}" checkboxName="visible" v-model="packageData.visible" @input="$v.$touch()" />
									</div>
								</div>
							</div>
						</div>
					</form>
				</div>
			</div>
		</div>
	</div>
</template>

<style lang="less" scoped>
label {
	display: block;
}
</style>

<script>
import Nav from '@/components/Nav.vue'
import PackageFileSelector from '@/components/PackageFileSelector'
import PackageIconSelector from '@/components/PackageIconSelector'
import SwitchCheckbox from '@/components/SwitchCheckbox'

import { PackageAPI } from '@/scripts/ApiUtil'
import { required } from 'vuelidate/lib/validators'

export default {
	name: "packageAdd",
	components: {
		Nav,
		PackageFileSelector,
		PackageIconSelector,
		SwitchCheckbox
	},
	data() {
		return {
			packageData: {
				name: "",
				identifier: "",
				shortDescription: "",
				detailedDescription: "",
				releaseVersion: "1.0",
				releaseDescription: "Initial Release",
				bugsReportURL: null,
				minOSVersion: null,
				maxOSVersion: null,
				visible: true,
			},
			selectedPackageIcon: null,
			selectedPackageFile: null,
			isDoingWork: false	
		}
	},
	validations: {
		packageData: {
			name: { required },
			identifier: { required },
			shortDescription: { required },
			detailedDescription: { required },
			releaseVersion: { required },
			releaseDescription: { required },
			minOSVersion: { required }
		},
		//selectedPackageIcon: { required },
		selectedPackageFile: { required }
	},
	mounted() {
		window.scrollTo(0,0);
	},
	methods: {
		selectIcon(e) {
			this.$v.$touch();
			this.selectedPackageIcon = e.target.files[0];
			const ref = this.$refs.iconPreview;

			const reader = new FileReader();
			reader.onload = function(progress) {
				const blob = window.URL.createObjectURL(BlobUtil.arrayBufferToBlob(progress.target.result));
				ref.style.backgroundImage = `url(${blob})`;
			}
			reader.readAsArrayBuffer(e.target.files[0]);
		},
		selectPackage(e) {
			this.$v.$touch();
			this.selectedPackageFile = e.target.files[0];
		},
		clearIcon() {
			this.$v.$touch();
			this.selectedPackageIcon = null;
			this.$refs.iconSelector.value = null;
			this.$refs.iconPreview.style.backgroundImage = null;
		},
		
		cancel() {
			const self = this;
			bootbox.confirm({
				title: "Canel?",
				message: "Are you sure you want to cancel? All unsaved progress will be lost.",
				buttons: {
					confirm: {
						label: 'OK',
						className: 'btn-danger'
					},
					cancel: {
						label: 'Cancel',
						className: 'btn-secondary'
					}
				},
				callback: async (result) => {
					if (result) {
						self.$router.replace(`/packages/`);
					}
				}
			});
		},
		async savePackage() {
			this.isDoingWork = true;
			
			let createdPackage = await PackageAPI.addPackage(this.packageData);
			if (createdPackage.status == 200) {
				if (this.selectedPackageIcon) {
					let iconUploadResult = await PackageAPI.uploadPackageIcon(createdPackage.data["package"].id, this.selectedPackageIcon);
					if (iconUploadResult.status != 200) bootbox.alert(`There was an error uploading the package icon.<br>You need to manually upload the package icon again through the Package editor.<br><br>${iconUploadResult.data.message}`);
				}
				
				let fileUploadResult = await PackageAPI.uploadPackageFile(createdPackage.data["package"].id, createdPackage.data["version"].id, this.selectedPackageFile);
				if (fileUploadResult.status != 200) bootbox.alert(`There was an error uploading the package file.<br>You need to manually upload the package file again through the Version manager.<br><br>${fileUploadResult.data.message}`);
				
				this.isDoingWork = false;
				this.$router.replace(`/packages/${createdPackage.data["package"].identifier}`);
			} else {
				this.isDoingWork = false;
				bootbox.alert(`There was an error creating the package.<br><br>${createdPackage.data.message}`);
			}
		}
	}
}
</script>